`, "",{"version":3,"sources":["webpack://./src/Settings.vue"],"names":[],"mappings":";AA0IA;CACA,aAAA;CACA,mBAAA;AACA;AAEA;CACA,YAAA;AACA;AAEA;CACA,iBAAA;AACA","sourcesContent":["<template>\n\t<div id=\"openhab\" class=\"section\">\n\t\t<h2 class=\"d-flex\">\n\t\t\tOpenhab Viewer&nbsp;\n\t\t\t<figure v-if=\"loading\" class=\"icon-loading loading\" />\n\t\t\t<figure v-if=\"!loading && success\" class=\"icon-checkmark success\" />\n\t\t</h2>\n\n\t\t<h3>{{t('openhab', 'Server settings')}}</h3>\n\t\t<fieldset class=\"d-flex\">\n\t\t\t<label class=\"d-flex\"><input type=\"radio\" v-model=\"settings['server.type']\" value=\"myopenhab\" @change=\"onChange\" />myopenhab.org</label>&nbsp;\n\t\t\t<label class=\"d-flex\"><input type=\"radio\" v-model=\"settings['server.type']\" value=\"custom\" @change=\"onChange\" />{{t('openhab', 'Custom')}}</label>\n\t\t</fieldset>\n\n\t\t<fieldset v-if=\"settings['server.type'] === 'custom'\">\n\t\t\t<label class=\"d-flex\">{{t('openhab', 'Server URL')}}:&nbsp;\n\t\t\t\t<input v-model=\"settings['server.url']\"\n\t\t\t\t\ttype=\"text\"\n\t\t\t\t\t@blur=\"onChange\">\n\t\t\t</label>\n\t\t</fieldset>\n\n\t\t<fieldset v-if=\"settings['server.type'] === 'custom'\">\n\t\t\t<label class=\"d-flex\">{{t('openhab', 'Ignore SSL issues?')}}&nbsp;\n\t\t\t\t<input v-model=\"settings['server.ignoreSSL']\"\n\t\t\t\t\ttype=\"checkbox\"\n\t\t\t\t\t@change=\"onChange\">\n\t\t\t</label>\n\t\t</fieldset>\n\n\t\t<fieldset v-if=\"settings['server.type'] === 'myopenhab'\">\n\t\t\t<label class=\"d-flex\">{{t('openhab', 'Username')}}:&nbsp;\n\t\t\t\t<input v-model=\"settings['server.username']\"\n\t\t\t\t\ttype=\"text\"\n\t\t\t\t\t@blur=\"onChange\">\n\t\t\t</label>\n\t\t</fieldset>\n\n\t\t<fieldset v-if=\"settings['server.type'] === 'myopenhab'\">\n\t\t\t<label class=\"d-flex\">{{t('openhab', 'Password')}}:&nbsp;\n\t\t\t\t<input v-model=\"settings['server.password']\"\n\t\t\t\t\ttype=\"password\"\n\t\t\t\t\t@blur=\"onChange\">\n\t\t\t</label>\n\t\t</fieldset>\n\n\t\t<h3>{{t('openhab', 'Additional settings')}}</h3>\n\t\t<fieldset>\n\t\t\t<label class=\"d-flex\">{{t('openhab', 'Refresh interval')}}:&nbsp;\n\t\t\t\t<input v-model=\"settings['server.interval']\"\n\t\t\t\t\ttype=\"number\"\n\t\t\t\t\t@blur=\"onChange\">\n\t\t\t\t{{t('openhab', 'seconds')}}\n\t\t\t</label>\n\t\t</fieldset>\n\t</div>\n</template>\n\n<script>\nimport axios from '@nextcloud/axios';\nimport { showError } from '@nextcloud/dialogs'\nimport { generateUrl } from '@nextcloud/router'\n\nconst SETTINGS = [\n\t'server.type',\n\t'server.url',\n\t'server.username',\n\t'server.password',\n\t'server.ignoreSSL',\n\t'server.interval',\n]\n\nexport default {\n\tname: 'Settings',\n\tdata: function() {\n\t\treturn {\n\t\t\tsettings: SETTINGS.reduce((obj, key) => ({ ...obj, [key]: '' }), {}),\n\t\t\tloading: false,\n\t\t\tsuccess: false,\n\t\t\ttimeout: null,\n\t\t}\n\t},\n\tasync created() {\n\t\ttry {\n\t\t\tconst response = await axios.get(generateUrl('/apps/openhab/settings'))\n\t\t\tthis.settings = response.data;\n\n\t\t\t// set initial value of server mode\n\t\t\tif (!this.settings['server.type']) {\n\t\t\t\tthis.settings['server.type'] = 'custom';\n\t\t\t\tthis.setValue('server.type', 'custom');\n\t\t\t}\n\t\t} catch (e) {\n\t\t\tshowError(this.t('openhab', 'Failed to load settings'))\n\t\t\tthrow e\n\t\t}\n\t},\n\tmethods: {\n\t\tonChange() {\n\t\t\tif (this.timeout) {\n\t\t\t\tclearTimeout(this.timeout)\n\t\t\t}\n\t\t\tthis.timeout = setTimeout(() => {\n\t\t\t\tthis.submit()\n\t\t\t}, 1000)\n\t\t},\n\t\tasync submit() {\n\t\t\tthis.loading = true\n\t\t\ttry {\n\t\t\t\tawait axios.put(generateUrl('/apps/openhab/settings'), {\n\t\t\t\t\tsettings: this.settings,\n\t\t\t\t})\n\t\t\t\tthis.success = true;\n\t\t\t} catch (e) {\n\t\t\t\tshowError(this.t('openhab', 'Failed to save settings'))\n\t\t\t\tthrow e\n\t\t\t}\n\t\t\tthis.loading = false\n\t\t\tsetTimeout(() => { this.success = false }, 3000)\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\t.d-flex {\n\t\tdisplay: flex;\n\t\talign-items: center;\n\t}\n\n\tinput[type='text'], input[type='password'] {\n\t\twidth: 250px;\n\t}\n\n\th3 {\n\t\tfont-weight: bold;\n\t}\n</style>\n"],"sourceRoot":""}]);
